  .Selectbox-list-item-checkbox::before {
      content: '';
      top: 50%;
      left: 50%;
      position: absolute;
      transform: translate(-45%, -55%) rotate(45deg);
      height: 55%;
      width: 25%;
      border-bottom: 1px solid #fff;
      border-right: 1px solid #fff;
      opacity: 0;
      transition: .5s;
