  .Selectbox-list-item.active .Selectbox-list-item-checkbox, .Selectbox-list-item:hover .Selectbox-list-item-checkbox {
      border: 1px solid #fff;
    }
  .Selectbox-list-item.active .Selectbox-list-item-checkbox::before {
      opacity: 1;
    }
  .Selectbox-list-submit {
    background-color: #adadad;
    height: 3.25rem;
    transform: translateY(100%);
