            appearance: none;
    border: none;
    border-radius: 0;
    background-color: #28416b;
    padding: .75rem;
    padding-top: 1.25rem;
    color: #fff;
  }
  .material .Selectbox-select {
      background-color: transparent;
      padding: 1rem 0 .75rem 0;
    }

.Selectbox.native .Selectbox-label {
    top: 50%;
    left: .75rem;
    transform: translate(0, -50%);
  }

.Selectbox.native .Selectbox-label.populated {
    transform: translateY(-150%);
  }

.Selectbox.native .Selectbox-arrow {
    top: 40%;
    right: .75rem;
    right: .75rem;
    transform: rotate(45deg) translateX(-50%);
  }

.Selectbox.material {
  border-bottom: 1.5px solid #eff0f4;
}

.Selectbox.material .Selectbox-trigger {
    background-color: transparent;
    padding: .75rem 0;
  }

.Selectbox.material::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  background-color: #28416b;
  transform: translate(-50%, 100%) scaleX(0);
  width: 100%;
  height: 2px;
  transition: transform .3s cubic-bezier(0, 0, .2, 1);
}

.Selectbox.material:hover {
  border-bottom: 2px solid #000;
}

.Selectbox.material.focus {
  border-bottom-color: transparent;
}

.Selectbox.material.focus::before {
  transform: translate(-50%, 100%) scaleX(1);
}

.Selectbox.native.material .Selectbox-label {
    left: 0;
  }

.Selectbox.native.material .Selectbox-arrow {
    right: 0;
  }

